
@import './tailwind.css';
@import './font.css';

:root {
  --header-height: 110px;
  --bottom-height: 122px;
  @media (max-width: 640px) {
    --header-height: 80px;
  }
}

body {
  font-family: 'Noto Serif JP', sans-serif;
  font-size: 20px;
  color: #404040;
  @media (max-width: 960px) {
    // font-size: 2vw;
  }
  @media (max-width: 500px) {
    font-size: 16px;
  }
}

h2 {
  font-size: 58px;
  @media (max-width: 960px) {
    font-size: 5.5vw;
  }
  // @media (max-width: 640px) {
  //   font-size: 40px;
  // }
}

h3 {
  font-size: 48px;
  @media (max-width: 960px) {
    font-size: 5vw;
  }
}

.auto-phrase {
  word-break: auto-phrase;
}

.tabs {
  .tab-button {
    background-color: #F5F5F5;
    cursor: pointer;
    @media screen and (max-width: 400px) {
      font-size: 12px;
    }
  }
  .tab-content {
    display: none;
  }
  &[data-active="1"] {
    .tab1-button {
      background-color: var(--color-main);
      color: white;
    }
    .tab1-content {
      display: block;
    }
  }
  &[data-active="2"] {
    .tab2-button {
      background-color: var(--color-main);
      color: white;
    }
    .tab2-content {
      display: block;
    }
  }
  &[data-active="3"] {
    .tab3-button {
      background-color: var(--color-main);
      color: white;
    }
    .tab3-content {
      display: block;
    }
  }
  &[data-active="4"] {
    .tab4-button {
      background-color: var(--color-main);
      color: white;
    }
    .tab4-content {
      display: block;
    }
  }
}

.max-960 {
  max-width: 960px;
  margin-left: auto;
  margin-right: auto;
  box-sizing: content-box;
  overflow: hidden;
  @media screen and (max-width: 960px) {
    // margin-left: 20px;
    // margin-right: 20px;
    max-width: unset;
  }
}

.gradient-text {
  background: linear-gradient(294.61deg, #01D5C8 15.71%, #0091B9 84.41%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  color: transparent;
}

.gradient-text2 {
  background: linear-gradient(103.2deg, #A47620 1.99%, #E1B263 49.11%, #A47620 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  color: transparent;
}


.swiper {
  .swiper-wrapper {
    height: fit-content;
    .swiper-slide {
      .before, .after {
        flex: 1;
        width: 100%;
        img {
          width: 100%;
        }
      }
    }
  }
  .swiper-button-next, .swiper-button-prev {
    width: 60px;
    height: 60px;
  }
}

.swiper-pagination {
  position: relative;
  margin-top: 40px;
}

.accordion-card {
  .accordion-card-detail {
    max-height: 0;
    padding: 0;
    overflow: hidden;
    transition: max-height 0.3s ease;
  }
  .accordion-card-header {
    cursor: pointer;
  }
  &.expanded {
    .accordion-card-detail {
      max-height: max-content;
      &.padding {
        padding: 20px;
      }
    }
    .toggle-icon {
      transform: rotate(180deg);
    }
  }
}

.faq-item {
  .faq-question {
    cursor: pointer;
    .question-mark {
      &:after {
        width: 30px;
        height: 30px;
        content: '﹢';
        color: #CCCCCC;
      }
    }
  }
  .faq-answer {
    max-height: 0;
    transition: max-height 0.3s ease, padding-block 0.3s ease, opacity 0.3s ease;
    padding: 0;
    margin-top: 0;
    overflow: hidden;
    opacity: 0;
  }
  &.expanded {
    .question-mark {
      &:after {
        content: '﹣';
      }
    }
    .faq-answer {
      opacity: 1;
      max-height: 400px;
      padding-block: 20px;
    }
  }
}

.nav-container {
  transform: translateX(100%);
  transition: transform 0.3s ease, opacity 0.3s ease;
  opacity: 0;
  &.active {
    opacity: 1;
    transform: translateX(0);
  }
}
.menu, .nav-close {
  cursor: pointer;
}

.header-menu, .bottom-menu {
  position: fixed;
  left: 0;
  width: 100%;
  z-index: 100;
  transition: transform 0.3s ease;
}

.header-menu {
  top: 0;
  height: var(--header-height);
  &.active {
    transform: translateY(-100%);
  }
}

.bottom-menu {
  bottom: 0;
  // height: var(--bottom-height);
  &.active {
    transform: translateY(100%);
  }
}

.main {
  padding-top: var(--header-height);
}
